
.inner-container {
  /*border: 1px solid black;*/
  margin-top: 30px;
  width: 50%;
  border: 11px solid #ff6a5f;
  border-radius: 5px;
  display: inline-block;
  position: relative;
}
.flex-container {
  display: flex;
  flex-direction: column;
}
.output-canvas {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
  font-family: Helvetica;
  color: #FFF;  
}
.webcam {
  width: 100%;
  height: 100%;
}

.text-status {
  text-transform: uppercase;
  color: #efefef;
  font-family: Tahoma,  sans-serif;
}

.text-camera {
  font-size: 110%;
  text-transform: uppercase;
  color: #efefef;
  font-family: Tahoma,  sans-serif;
}
